}

.contact-in-input:-ms-input-placeholder {
	color: #333;
}

.contact-in-input::placeholder {
	color: #333;
}

.contact-in-textarea {
	width: 100%;
	height: 140px;
	margin-bottom: 20px;
	border: 1px solid #333;
	outline: none;
	padding-top: 5px;
	padding-left: 5px;
	background: transparent;
	color: #333;
	font-size: 12px;
	font-weight: 300;
	font-family: 'Poppins', sans-serif;
}

.contact-in-textarea::-webkit-input-placeholder {
	color: #333;
}

.contact-in-textarea:-ms-input-placeholder {
	color: #333;
}

.contact-in-textarea::placeholder {
	color: #333;
}

.contact-in-btn {
	width: 100%;
	height: 40px;
	border: 1px solid #333;
	outline: none;
	background: transparent;
	color: #333;
	font-size: 12px;
	font-weight: 300;
	font-family: 'Poppins', sans-serif;
	cursor: pointer;
}


@media only screen and (max-width:480px) {
	.contact-in:nth-child(1) {
		-webkit-flex: 50% 1;
		        flex: 50% 1;
	}

	.contact-in:nth-child(2) {
		-webkit-flex: 50% 1;
		        flex: 50% 1;
	}

	.contact-in:nth-child(3) {
		-webkit-flex: 100% 1;
		        flex: 100% 1;
	}

}

@media only screen and (max-width:360px) {
	.contact-in:nth-child(1) {
		-webkit-flex: 50% 1;
		        flex: 50% 1;
	}

	.contact-in:nth-child(2) {
		-webkit-flex: 50% 1;
		        flex: 50% 1;
	}

	.contact-in:nth-child(3) {
		-webkit-flex: 100% 1;
		        flex: 100% 1;
	}

}

